  input[type=range] {
    height: 26px;
    -webkit-appearance: none;
    margin: 10px 0;
    width: 80%;
  }
  input[type=range]:focus {
    outline: none;
  }
  input[type=range]::-webkit-slider-runnable-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 1px 1px 2px #B8B8B8;
    background: #FF7E6B;
    border-radius: 1px;
    border: 0px solid #FFFFFF;
  }
  input[type=range]::-webkit-slider-thumb {
    box-shadow: 1px 1px 2px #C2C2C2;
    border: 1px solid #FFFFFF;
    height: 18px;
    width: 18px;
    border-radius: 25px;
    background: #FF7E6B;
    cursor: pointer;
    -webkit-appearance: none;
    margin-top: -7px;
  }
  input[type=range]:focus::-webkit-slider-runnable-track {
    background: #FF7E6B;
  }
  input[type=range]::-moz-range-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    box-shadow: 1px 1px 2px #B8B8B8;
    background: #FF7E6B;
    border-radius: 1px;
    border: 0px solid #FFFFFF;
  }
  input[type=range]::-moz-range-thumb {
    box-shadow: 1px 1px 2px #C2C2C2;
    border: 1px solid #FFFFFF;
    height: 18px;
    width: 18px;
    border-radius: 25px;
    background: #FF7E6B;
    cursor: pointer;
  }
  input[type=range]::-ms-track {
    width: 100%;
    height: 5px;
    cursor: pointer;
    animate: 0.2s;
    background: transparent;
    border-color: transparent;
    color: transparent;
  }
  input[type=range]::-ms-fill-lower {
    background: #FF7E6B;
    border: 0px solid #FFFFFF;
    border-radius: 2px;
    box-shadow: 1px 1px 2px #B8B8B8;
  }
  input[type=range]::-ms-fill-upper {
    background: #FF7E6B;
    border: 0px solid #FFFFFF;
    border-radius: 2px;
    box-shadow: 1px 1px 2px #B8B8B8;
  }
  input[type=range]::-ms-thumb {
    margin-top: 1px;
    box-shadow: 1px 1px 2px #C2C2C2;
    border: 1px solid #FFFFFF;
    height: 18px;
    width: 18px;
    border-radius: 25px;
    background: #FF7E6B;
    cursor: pointer;
  }
  input[type=range]:focus::-ms-fill-lower {
    background: #FF7E6B;
  }
  input[type=range]:focus::-ms-fill-upper {
    background: #FF7E6B;
  }
  
